<template>
	<VideoPlayer
		v-if="isMounted"
		:chronic-id="propData.chronicId"
		:mode="propData.mode"
  	/>	
</template>

<script>
import VideoPlayer from 'vuejs-video-player/src/Component.vue';
export default {
  name: 'App',
  components: {
    VideoPlayer
  },
  data() {
    return {
      propData: {

      },
      isMounted: false,
    }
  },
  mounted() {
    this.propData = JSON.parse(JSON.stringify(this.$el.parentElement.dataset));
    this.isMounted = true;
  }
}
</script>
